@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing : border-box ;
    font-family: 'Poppins', sans-serif;
}
.App{
    display: flex;
    flex-flow: column;
    align-items : center ;
    justify-content:center ;
}

.app__searchform{
margin-top: 18px ;
}

.app__input{
    padding: 6px 16px;
    min-width: 200px;
    max-width:100vw;
    font-size: 15px;
}

.app__submit{
    font-size: 15px;
    padding: 6px 16px;
    color: white;
    background: #02aff0 ;
    margin-left: 6px ;
    border: 2px solid #02aff0;  
    border-radius:4px ;
    transition: 0.25s;
    cursor: pointer;
}
.app__submit:hover{
    color : #20aff0 ;
    background : white ;
    cursor: pointer;
}
.app__recipes{
    margin-top: 24px;
    display: Grid;
    grid-template-columns : 230px 230px ;
    align-items: baseline;
}
.app__healthLabels{
    padding: 7px 8px;
    margin-left: 6px;
    border-radius: 4px ;
}
.recipeTile__name{
    max-width: 200px;
    text-align: center;
    margin: 12px 0px;
}
.recipeTile__img{
    height:200px ;
    width:200px ;
    object-fit: cover ;
    border-radius:4px ;
    cursor: pointer;
}
